.navBar .navigation {
  height: 80px;
  justify-content: center;
  align-items: center;
  z-index: 999;
  display: flex;
}

.navBar .mobileNavigation {
  display: none;
}
.mobileNavigation {
  margin-left: 48px;
  padding-top: 25px;
}
@media (max-width: 500px) {
  .navBar .navigation {
    display: none;
  }

  .navBar .mobileNavigation {
    display: flex;
    align-items: center;
    margin-left: 149px;
    margin-right: 17px;
    margin-top: -35px;
  }

  .navBar .mobileNavigation .Hamburger {
    position: absolute;
    left: 1200px;
    cursor: pointer;
  }
}
@media (max-width: 414px) {
  .navBar .navigation {
    display: none;
  }

  .navBar .mobileNavigation {
    display: flex;
    align-items: center;
    margin-left: -33px;
    margin-right: 17px;
    margin-top: -53px;
  }
}
@media (max-width: 360px) {
  .navBar .navigation {
    display: none;
  }

  .navBar .mobileNavigation {
    margin-left: 98px;
  }
}
